body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.APKTitle{
    background-image: url(/static/media/tindev.27a0486c.jpg);
    padding: 8%;
    margin: 0%;
    font-size: 330%;
    color: aliceblue;
    background-clip: border-box;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: right top;
    background-attachment: fixed;
}
.cls-1{fill: #fff;}
.cls-2{fill: none;}
.cls-3{fill: #4c4c4c;}
.logodev{
    width: 25%;
    margin:auto;
    display: block;
}
.caption{
    color: slategray;
    font-size: 80%;
    margin-top: 0%;
}
.caption a{
    color: slategray;
}
.title{
    color: #4A4A4A;
    -webkit-transition: color 0.3s ease;
    transition: color 0.3s ease;
    font-size: 3.2rem;
    line-height: 1.15em;
    font-family: "Proxima Nova Soft", sans-serif;
    text-rendering: geometricPrecision;
    font-weight: 300;
    text-align: left;
    padding-left: 8%;
    margin: 0%;
}
.News{
    background-image: -webkit-linear-gradient(left, #FD297B,#ff5864,#FF655B);
    background-image: linear-gradient(to right, #FD297B,#ff5864,#FF655B);
}

.newsArticle{
    background-color: whitesmoke;
    margin: 3% 7%;
    padding-top: 1%;
    border-radius: 100px
}

  .newsTitle{
    font-family: "ProximaNova", sans-serif;
    font-size: 18px;
    font-weight: 800;
    max-height: 64.8px;
    letter-spacing: normal;
    line-height: 1.14;
    box-sizing: border-box;
    display: inline;
    padding-right: 12px;
    left: -8px;
    text-align: left;
    text-decoration: none;
    text-transform: uppercase;
    color: black;
  }

  .lowerInfo{
    display: block;
    margin: 1.75rem 0 0 0;
    font-family: "Proxima Nova Soft", sans-serif;
    font-size: 0.7rem;
    line-height: 2.0rem;
    color: #9EABB3;
  }
.UX{
    background-color: white;
    padding: 1% 8%;
}

.UXtitle{
    font-family: "ProximaNova", sans-serif;
    font-weight: 800;
    letter-spacing: -0.5px;
    font-size: 2.5em;
    text-align: left;
}

.paragraph{
    text-align: left;
    font-weight: 400;
    line-height: 1.6;
    font: 112.5%/1.45em georgia,serif;
}

.undertext{
    font-weight: 400;
    color: rgb(124, 124, 124);
    font-size: 1.5em;
    text-align: left;
    margin: 0;
    font-family: "ProximaNova", sans-serif;
}

.link{
    text-decoration: none;
    opacity: 0.8;
    border-bottom: 2px solid rgb(253, 80, 104);
    -webkit-transition: border-bottom 150ms linear 0s;
    transition: border-bottom 150ms linear 0s;
    color: rgb(0, 0, 0);
}
.link:hover{
    border-bottom: 2px solid rgb(35, 157, 218);
}

  .sectionTitle {
    font-family: "ProximaNova", sans-serif;
