.News{
    background-image: linear-gradient(to right, #FD297B,#ff5864,#FF655B);
}

.newsArticle{
    background-color: whitesmoke;
    margin: 3% 7%;
    padding-top: 1%;
    border-radius: 100px
}

  .newsTitle{
    font-family: "ProximaNova", sans-serif;
    font-size: 18px;
    font-weight: 800;
    max-height: 64.8px;
    letter-spacing: normal;
    line-height: 1.14;
    box-sizing: border-box;
    display: inline;
    padding-right: 12px;
    left: -8px;
    text-align: left;
    text-decoration: none;
    text-transform: uppercase;
    color: black;
  }

  .lowerInfo{
    display: block;
    margin: 1.75rem 0 0 0;
    font-family: "Proxima Nova Soft", sans-serif;
    font-size: 0.7rem;
    line-height: 2.0rem;
    color: #9EABB3;
  }